import subject from "@/api/subject";
import blog from "@/api/blog";
import Tinymce from '@/components/Tinymce/index'
export default {
  components: { Tinymce }, //声明组件
  layout: "ucenterLayout",
  name: "",
  data() {
    return {
      flagdialog: "1",
      blogInfo: {
        title: "",
        subjectId: "", //二级分类id
        subjectParentId: "", //一级分类id
        content: "",
        firstPicture:
          "https://underdogedu.oss-cn-beijing.aliyuncs.com/%E7%B4%A0%E6%9D%90/1525939573202.jpg",
        zsPicture:
          "https://underdogedu.oss-cn-beijing.aliyuncs.com/%E7%B4%A0%E6%9D%90/88abf95a1dc9325bcb6c3de8001e2e2.jpg"
      },
      subjectOneList: [], //一级分类
      subjectTwoList: [], //二级分类
      blogList: [],
      enjoyList: [],
      BASE_API: 'http://natapp.feifu.top' // 接口API地址
    };
  },
  methods: {
    handleSelect(index) {
      this.flagdialog = index;
    },
    //点击某个一级分类，出发change显示对应二级分类  value默认为id值
    subjectLevelOneChanged(value) {
      //遍历所有的分类，包含一级和二级
      for (var i = 0; i < this.subjectOneList.length; i++) {
        //每一个一级分类
        var oneSubject = this.subjectOneList[i];
        //判断 所有一级分类id和所点击一级分类id是否一样
        if (value === oneSubject.id) {
          //从一级分类中获取所有的二级分类
          this.subjectTwoList = oneSubject.children;
          this.blogInfo.subjectId = "";
        }
      }
    },

    // 上传封面成功调用的方法
    handleAvatarSuccess(res, file) {
      this.blogInfo.firstPicture = res.data.url;
    },
    // 上传封面成功调用的方法
    handleAvatarSuccessForZS(res, file) {
      this.blogInfo.zsPicture = res.data.url;
    },
    // 上传之前调用的方法
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    // 上传之前调用的方法
    beforeAvatarUploadForZS(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    //查询所有的一级分类
    getOneSubject() {
      subject.getSubjectList().then(response => {
        this.subjectOneList = response.data.data.list;
        console.log(this.subjectOneList);
      });
    },
    saveOrUpdate() {
      //判断添加还是修该
      if (!this.blogInfo.id) {
        //添加
        this.addblog();
      } else {
        this.updateblog();
      }
    },
    //添加博客
    addblog() {
      blog.addBlogInfo(this.blogInfo).then(response => {
        this.$message({
          type: "success",
          message: "添加博客信息成功!"
        });
        location.reload();
      });
    },
    handleDelete(id) {
      this.$confirm("此操作将永久删除该记录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          return blog.deleteDataById(id);
        })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          location.reload();
        })
        .catch(response => {
          // 失败
          if (response === "cancel") {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          } else {
            this.$message({
              type: "error",
              message: "删除失败"
            });
          }
        });
    },
    //修该博客
    updateblog() {
      blog.updateblogInfoId(this.blogInfo).then(response => {
        //
        this.$message({
          type: "success",
          message: "修该博客信息成功!"
        });
        location.reload();
      });
    },
    getBlogByUserId() {
      blog.getBlogByUserId().then(response => {
        this.blogList = response.data.data.list;
        console.log(this.blogList);
      });
    },
    handleEdit(id) {
      this.blogInfo.id = id;
      blog.getBlogInfo(id).then(response => {
        this.blogInfo = response.data.data.eduBlog;
        console.log(this.blogInfo);
      });
      this.flagdialog = "2";
    },
    async EnjoyBlogList() {
      const res = await blog.EnjoyBlogList();
      this.enjoyList = res.data.data.list;
    }
  },
  created() {
    this.getOneSubject();
    this.getBlogByUserId();
    this.EnjoyBlogList();
  }
};
